@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500&display=swap);
body{font-family:'Noto Sans KR', sans-serif;-ms-overflow-style:none;margin:0px;overflow:scroll;scroll-behavior:smooth;display:flex;-webkit-font-smoothing:subpixel-antialiased;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;-moz-user-select:none;user-select:none}#root{width:100%}::-webkit-scrollbar{display:none}.header{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:100%;justify-content:center;align-items:center;top:0px;right:0px;flex-direction:column;display:flex;background-color:white}.header a{font-weight:400;font-size:20px;padding:0 15px;color:#010101;text-decoration:none}.slider-wrap{width:100%;overflow:hidden;height:330px}.slider-wrap .game{outline:none;display:flex;width:750px;height:330px;overflow:hidden;justify-content:center}.slider-wrap .game .gameBanner,.slider-wrap .game img{margin:0 auto}.slider-wrap .game .gameBanner{position:absolute;display:flex;justify-content:center;align-items:center;z-index:1001;width:750px;height:330px;transition:background-color 0.25s ease-in-out}.slider-wrap .game .gameBanner .gameLink{cursor:pointer;text-decoration:none;color:black}.slider-wrap .game .gameBanner+img{transform:scale(1);transition:transform 0.25s ease-in-out}.slider-wrap .game .gameBanner:hover{background-color:rgba(255,255,255,0.8)}.slider-wrap .game .gameBanner:hover+img{transform:scale(1.1);z-index:-1}.game-page{display:flex;align-items:center;justify-content:center;flex-direction:column;width:100%}.game-page.nightMare .game-illust01{background-color:black}.game-page.nightMare .game-intro{color:white}.game-page .game-title,.game-page .game-char{align-items:center;justify-items:center;pointer-events:none}.game-page .down-btn{cursor:pointer;box-sizing:border-box;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:transparent;border:2px solid;border-radius:0.6em;display:flex;align-self:center;font-size:1rem;font-weight:400;line-height:1;margin:20px;padding:1.2em 2.8em;text-decoration:none;text-align:center;text-transform:uppercase;font-family:'Montserrat', sans-serif;font-weight:700;border-color:#f1c40f;color:#fff;background-image:linear-gradient(45deg, #f1c40f 50%, transparent 50%);background-position:100%;background-size:400%;transition:background 300ms ease-in-out}.game-page .down-btn:hover,.game-page .down-btn:focus{background-position:0;color:#fff;outline:0}.game-page .game-illust01{pointer-events:none;top:0px;right:0px;width:100%;height:100%;position:fixed;z-index:-1}.game-page .game-illust01 img{opacity:0.4;-webkit-filter:blur(5px);filter:blur(5px);width:100%;height:100%}.fade-in{-webkit-animation:fade-in 1.2s ease-in-out both;animation:fade-in 1.2s ease-in-out both}@-webkit-keyframes fade-in{0%{opacity:0}100%{opacity:1}}@keyframes fade-in{0%{opacity:0}100%{opacity:1}}

